.main-navbar{
    margin-top: 25px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid rgb(184, 184, 184);
}
.navbar-logo img{
    width: 100px;
    height: 50px;   
}
.navlinks-ul{
    display: flex;
    width: 300px;
    padding: 0px 20px;
    justify-content: space-between;
    list-style: none;
    text-decoration: none;
    color: black;

}
.navlinks-ul a{
    color: rgb(109, 105, 105);
    text-decoration: none;
    font-size: 20px;
    padding: 13px 18px;
    border-radius: 10px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    transition: all 0.3s ease-in-out;
}
.navlinks-ul a:hover{
    background-color: rgb(143, 141, 141);
    color: aliceblue;
    padding: 13px 18px;
    border-radius: 10px;
    transition: all 0.3s ease-in-out;
}